<template>
  <div>
    <component @toggle="toggle" :is="type === 'buyer' ? 'Buyers' : 'Seller'" />
  </div>
</template>

<script>
import Buyers from '../../components/buyers-personal/buyers-personal'
import Seller from '../../components/seller-personal/seller-personal'

export default {
  name: 'personal',
  components: {
    Buyers,
    Seller
  },
  data() {
    return {
      type: 'buyer'
    }
  },
  created() {
    this.$jsCallNative('setStatusBarTintStyle', {
      style: 'LIGHT'
    })
  },
  beforeDestroy () {
    this.$jsCallNative('setStatusBarBackColor', {
      color: '#ffffff'
    })
    this.$jsCallNative('setStatusBarTintStyle', {
      style: 'DARK'
    })
  },
  deactivated() {
    this.$jsCallNative('setStatusBarBackColor', {
      color: '#ffffff'
    })
    this.$jsCallNative('setStatusBarTintStyle', {
      style: 'DARK'
    })
  },
  activated() {
    this.$jsCallNative('setStatusBarBackColor', {
      color: this.type === 'buyer' ? '#dfc895' : '#534e64'
    })
    this.$jsCallNative('setStatusBarTintStyle', {
      style: 'LIGHT'
    })
  },
  methods: {
    toggle() {
      this.type = this.type === 'buyer' ? 'seller' : 'buyer'
      this.$jsCallNative('setStatusBarBackColor', {
        color: this.type === 'buyer' ? '#dfc895' : '#534e64'
      })
    }
  }
}
</script>

