<script>
import { post } from '../../common/js/http'
import { mapState } from 'vuex'
import { getItem } from '../../common/js/storage'

export default {
  name: 'seller-personal',
  data() {
    return {
      info: {
        user: {}
      }
    }
  },
  created() {
    this.$jsCallNative('setStatusBarBackColor', {
      color: '#534e64'
    })
    this.getInfo()
  },
  computed: {
    ...mapState(['userInfo'])
  },
  methods: {
    getInfo() {
      const uid = getItem('uid')
      post('/shop/shop/shopHomePageData', { shopid: uid })
        .then(({ data: { data } }) => {
          this.info = data
        })
    }
  }
}
</script>

