  <div class="buyers">
    <div class="top-body">
      <div class="header">
        <van-icon @click="$router.goBack()" class="icon-left" name="arrow-left" />
        <h1>直播小店<span>（买家）</span></h1>
        <div class="exchange">
          <img v-if="info.isShop" @click="$emit('toggle')" src="./change@3x.png" alt="">
        </div>
      </div>
      <div class="links">
        <router-link to="/shopping">
          <p>购物车</p>
