        </router-link>
      </div>
      <div class="order-type">
        <h4 @click="$router.push('/order/list/0')">全部订单 <span>查看<van-icon name="arrow" /></span></h4>
        <div class="order-link">
          <router-link to="/order/list/1">
            <img src="./daifukuan@3x.png" alt="">
            <p>待付款</p>
            <span v-if="info.unpay_count">{{ info.unpay_count }}</span>
          </router-link>
          <router-link to="/order/list/2">
            <img src="./daifukuan@3x.png" alt="">
            <p>待发货</p>
            <span v-if="info.undelivery_count">{{ info.undelivery_count }}</span>
          </router-link>
          <router-link to="/order/list/3">
