            <p>待收货</p>
            <span v-if="info.unreceive_count">{{ info.unreceive_count }}</span>
          </router-link>
          <router-link to="/order/list/4">
            <img src="./tuikuan@3x.png" alt="">
            <p>退款</p>
          </router-link>
        </div>
      </div>
    </div>
    <div @click.prevent="goOpen" to="/store/open" class="open-link" v-if="!info.isShop">
      <p>我要开店</p>
      <span>立即开通直播小店 <van-icon name="arrow" /></span>
    </div>
  </div>
</template>

<script>
import { post } from '../../common/js/http'
import { mapState, mapMutations } from 'vuex'

export default {
  name: 'personal',
  data() {
    return {
      info: {}
    }
  },
  created() {
    this.$jsCallNative('setStatusBarBackColor', {
      color: '#dfc895'
    })
    this.getInfo()
  },
  computed: {
    ...mapState(['userInfo'])
  },
  methods: {
    ...mapMutations(['changeUserInfo']),
    getInfo() {
      post('/shop/user/getUserInfo')
        .then(({ data: { data } }) => {
          this.info = data
          this.changeUserInfo(data)
        })
    },
    goOpen() {
      if (!this.userInfo.is_anchor) {
        this.$toast.fail('请先申请成为主播')
      } else {
        this.$router.push('/store/open')
      }
    }
  }
}
</script>

